//Custom functions
$colorsPal: (
  light: $light,
  primary: $primary,
  link: $link,
  info: $info,
  success: $success,
  warning: $warning,
  danger: $danger,
  white: $white,
  black: $black,
);
@each $colorname, $color-bg in $colorsPal {
  .is-muted-#{$colorname} {
    background-color: lighten($color: $color-bg, $amount: 10%);
  }
}
.is-vcentered {
  align-items: center;
}
@each $colorname, $color-bg in $colorsPal {
  .is-highlight-#{$colorname} {
    text-decoration: none;
    color: transparent;
    box-sizing: border-box;
    text-decoration: none;
    box-shadow: inset 0 -2px 0 lighten($color-bg, 60%);
    &:hover {
      box-shadow: inset 0 -50px 0 0 lighten($color-bg, 45%);
      transition: all 0.2s ease-in-out;

      &::after {
        margin-right: 20px;
        content: attr(title);
        margin-left: -5px;
        color: lighten($color-bg, 100%);
        font-style: italic;
        font-weight: bold;
        font-size: x-small;
        transition: all 0.2s ease-in-out;
      }
    }
  }
}
@each $colorname, $color-bg in $colorsPal {
  .card-footer-#{$colorname} {
    background-color: lighten($color: $color-bg, $amount: 10%);
    align-items: center;
    justify-content: center;
    display: flex;
  }
}
@each $colorname, $color-bg in $colorsPal {
  .bold-#{$colorname} {
    font-weight: bold;
    font-family: $family-sans-serif;
    color: $color-bg;
    &:hover {
      color: lighten($color: $color-bg, $amount: 10%);
    }
  }
}

// Some custom SASS
body,
html {
  word-break: keep-all !important;
  line-height: 1.15;
  user-select: none;
  scroll-behavior: smooth;
}

.is-avatar {
  vertical-align: middle;
  height: 5vh;
  margin-left: 25px;
  image-rendering: crisp-edges;
  padding: 2em;
  width: 15vw;
  @media screen and (max-width: 1023px) {
    width: auto;
    max-width: 70%;
    margin: auto !important;
    vertical-align: middle;
    height: 5vh;
  }
}

.is-avatar-hero {
  vertical-align: middle;
  img {
    image-rendering: crisp-edges;
  }

  background-size: cover !important;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  border-radius: 50%;
}

.tag-footer {
  justify-content: space-between;
  overflow-x: auto;
}
::-webkit-scrollbar {
  width: 7px !important;
  height: 7px !important;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px transparentize($background, 0.7) !important;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px transparentize($background, 0.1) !important;
}
::-webkit-scrollbar-thumb:horizontal {
  -webkit-box-shadow: inset 0 0 6px transparentize($background, 0.2) !important;
}
button {
  a {
    color: #eff0eb;
  }
}
.tile {
  justify-content: center;
}

.is-hcentered {
  justify-content: space-around;
}
.is-family-mono {
  font-family: 'Roboto Mono', monospace;
}
.hr-match {
  display: inline-block;
  &:after {
    content: '';
    display: block;
    border-top: 5px solid $link;
    transform: skewX(-50deg);
    margin-right: 10px;
  }
}

.number-about {
  font-size: 1.4rem;
  color: $link;
}
.has-no-bg {
  background: transparent !important;
}
.progress {
  width: 55%;
}
#hero-content {
  z-index: 0;
  backdrop-filter: opacity(0.7) contrast(1.8);
}
.is-light-muted-bg {
  background-color: transparentize($color: $beige-light, $amount: 0.6);
  border-radius: 2px;
}
.is-muted-bg {
  background-color: transparentize($color: $dg, $amount: 0.5);
  border-radius: 2px;
}
.is-active {
  background-color: transparentize($color: $link, $amount: 0.7);
  border-radius: 20px;
  a {
    color: $white !important;
  }
}
.is-inactive {
  background-color: transparentize($color: $link, $amount: 0.9);
  border-radius: 20px;
  a {
    color: $white !important;
  }
}

.level:not(:last-child) {
  margin-bottom: 0.4rem;
}
.custom-width {
  max-width: 26.5vw;
}
.slick-prev,
.slick-next {
  &::before {
    color: #3c3c3b !important;
  }
}

//MEDIA Queries

@media screen and (max-width: 1023px) {
  .navbar-menu {
    background-color: #f05c57;
    box-shadow: 0 8px 16px rgba(10, 10, 10, 0.1);
    padding: 0.5rem 0;
  }
  #MainIcon {
    height: 156px;
    width: 156px;
  }
  body,
  html {
    overflow-x: hidden;
  }
  #content-main {
    padding: 0 2vmax;
  }
  .level-left + .level-right {
    margin-top: 0.2rem;
  }
  .level:not(:last-child) {
    margin-bottom: 0.8rem;
  }
  .custom-width {
    max-width: 100%;
  }
  .hero.is-fullheight {
    min-height: calc(100vh - #{$navbar-height});
  }
  canvas {
    max-height: calc(100vh - #{$navbar-height});
    min-width: calc(100vw - 0);
  }

  .progress {
    max-width: 35% !important ;
  }
}

.loader--hide {
  opacity: 0;
  z-index: -9999999;
  display: none;
}
.spinner {
  margin: 200px auto;
  width: 60px;
  height: 60px;
  position: relative;
  text-align: center;
  -webkit-animation: sk-rotate 2s infinite linear;
  animation: sk-rotate 2s infinite linear;
}
.dot1,
.dot2 {
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  top: 0;
  background-color: $link;
  border-radius: 100%;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.dot2 {
  top: auto;
  bottom: 0;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

@-webkit-keyframes sk-rotate {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes sk-rotate {
  100% {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
  }
}

@-webkit-keyframes sk-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
.vertical-timeline::before {
  width: 3px !important;
  background: $link !important;
}
.vertical-timeline-element-content-arrow {
  border-right: 7px solid $link !important;
}
